.messageWrapper{display:flex;width:300px}.messageWrapper:not(:first-child){margin-top:3px}.messageWrapper .triangle{display:inline-block;border-top:rgba(0,0,0,0) solid 30px;border-bottom:rgba(0,0,0,0) solid 30px;border-right:rgba(0,0,0,0) solid 30px}.messageWrapper .textWrapper{position:relative;padding:0 10px;flex-grow:1;display:flex;flex-direction:column;justify-content:center}.messageWrapper .textWrapper .title{line-height:.8;font-size:20px}.messageWrapper .textWrapper .closeButton{position:absolute;top:0;right:5px;opacity:.5;cursor:pointer}.messageWrapper .textWrapper .closeButton:hover{opacity:1}.messageWrapper .textWrapper .closeButton::after{content:"â®¾";font-size:20px}.error>div:first-child{border-right:rgba(255,0,0,.5) solid 30px}.error>div:last-child{background-color:rgba(255,0,0,.5)}.warning>div:first-child{border-right:rgba(253,150,0,.5) solid 30px}.warning>div:last-child{background-color:rgba(253,150,0,.5)}.info>div:first-child{border-right:rgba(4,248,4,.5) solid 30px}.info>div:last-child{background-color:rgba(4,248,4,.5)}